<template>
  <div class="jobsInfo-wrapper">
    <div class="jobsInfo-wrapper-head">
      <img
        class="mainBacgroundImg"
        src="https://picsum.photos/id/852/200/300?blur=2"
        alt="BacgroundImg"
      />
      <div class="CompanyLogo">
        <img
          v-if="JobInfoData.image"
          :src="'https://1kadr-api.pixyz.uz/' + JobInfoData.image"
          alt=""
        />
        <img
          v-else
          src="https://stories.td.com/volumes/default/article/221.gif"
          alt=""
        />
      </div>
    </div>
    <div class="jobsInfo-wrapper-content">
      <div class="jobsInfo-wrapper-content-titles">
        <div>
          <h3>{{ JobInfoData.title }}</h3>
          <p>
            Manzil: <span>{{ JobInfoData.company.address }}</span>
          </p>
        </div>
        <div class="jobsInfo-wrapper-content-titles-left">
          <div>
            <p
              :class="[
                'jobsInfo-wrapper-content-titles-left-copy-info-text',
                animatonClass,
              ]"
            >
              Copied
            </p>
            <button @click="shareLinkCopy">
              <span class="icon icon-share"></span>
            </button>
          </div>
          <p>{{ JobInfoData.created_at }} qoyilgan</p>
        </div>
      </div>
      <div class="jobsInfo-wrapper-content-minitabel">
        <div>
          <h3>Ish Tajribasi</h3>
          <p>Eng kami 1 Yill</p>
        </div>
        <div>
          <h3>Ish Darajasi</h3>
          <p>Senior Level</p>
        </div>
        <div>
          <h3>Ish Vaqti</h3>
          <p>{{ JobInfoData.job_type }}</p>
        </div>
        <div>
          <h3>Ish Haqqi</h3>
          <p>${{ JobInfoData.salary }}/ oyiga</p>
        </div>
      </div>
      <div class="jobsInfo-wrapper-content-paragrph">
        <div>
          <h3>Kompanya Haqida</h3>
          <p>
            {{ JobInfoData.company.about }}
          </p>
        </div>
        <div>
          <h3>Ish Haqida</h3>
          <p>{{ JobInfoData.opportunities }}</p>
        </div>
        <div>
          <h3>Talablar</h3>
          <p>{{ JobInfoData.requirements }}</p>
        </div>
      </div>
    </div>
    <div v-if="loading" class="loader-wrapper">
      <span class="icon icon-loading"></span>
    </div>
  </div>
</template>

<script>
import Api from '@/api/Api';

export default {
  name: 'JobsViewComponent',
  data() {
    return {
      JobInfoData: {
        id: 3,
        category: 2,
        title: 'Frontend JavaScript Dasturchisis kerak',
        image: '/media/vacancy_pictures/1617555129.jpg',
        company: {
          id: 6,
          name: 'PixYz',
          about:
            'PixYz Kampanyasi onlany internet sahifalarni ishlab chiqiqaradi Bularga Web Android Va hokazo',
          region: null,
          address: 'Bodomzor tumani',
          website: 'https://pixyz.uz/en/',
          contacts: [
            {
              id: 6,
              contact_type: null,
              email: 'pixyz@gmail.com',
              phone: '+998998998999',
              url: 'https://t.me/AzamMamatmurodov',
              company: 6,
            },
          ],
        },
        salary: '150.00',
        job_type: 'full_time',
        level: null,
        requirements: 'Tajripali Frontend Developer kerak',
        technologies: [
          {
            id: 1,
            name: 'NodeJs',
          },
          {
            id: 3,
            name: 'JavaScript',
          },
          {
            id: 6,
            name: 'Html',
          },
          {
            id: 7,
            name: 'Css',
          },
          {
            id: 8,
            name: 'Scss',
          },
        ],
        experience: 1,
        opportunities:
          'qulay ish joyi\r\nqulay ish joyi\r\nqulay ish joyi\r\nqulay ish joyi',
        is_confirmed: false,
        is_active: true,
        created_at: '2022-04-11',
      },
      loading: false,
      animatonClass: '',
    };
  },
  methods: {
    async getVacsineData() {
      this.loading = true;
      const response = await Api.getVacancieData(this.$route.params.id);
      this.JobInfoData = response.data;
      this.loading = false;
    },
    async shareLinkCopy() {
      var copyText = window.location;
      await navigator.clipboard.writeText(copyText);
      this.animatonClass = 'animatonClass';
      setTimeout(() => {
        this.animatonClass = '';
      }, 2100);
    },
  },
  mounted() {
    this.getVacsineData();
  },
  watch: {
    $route() {
      this.getVacsineData();
    },
  },
};
</script>

<style src="./JobsInfoComonent.scss" lang="scss"></style>
